import {
	mapState,
	mapActions
} from 'vuex' //引入vuex
// 瀑布流组件
import WaterfallFlow from '../../components/nairenk-waterfall-flow/nairenk-waterfall-flow.vue';
export default {
	data() {
		return {
			tab: 0,
			loading: true
		}
	},
	components: {
		WaterfallFlow
	},
	computed: {
		arr(){
			return this.$store.state.Menu.arr;
		},	
		recommendTpl() {
			return this.$store.state.Menu.list;
		},
	},
	// 触底
	onReachBottom() {
		var self = this;
		self.loading = true;
		self.shudonglower();
		self.fnNewList();
	},
	//下拉刷新
	 onPullDownRefresh() {
	        var self = this;
			uni.showNavigationBarLoading();
			self.shudonRefresh(self.item);
			self.$nextTick(function() {
				self.fnNewList();
			})
	        setTimeout(function () {
	            uni.stopPullDownRefresh();
				uni.hideNavigationBarLoading();
	        }, 1000);
	    },
	async onLoad() {
		this.fnNewList();
		this.userDetails()
	},
	methods: {
		//导航点击
		changeItem(index) {
			this.tab = index;
			this.shudongAction(index)
			this.fnNewList();
		},
		//获取列表
		async fnNewList() {
			if (this.$store.state.Menu.list == '') {
				await this.MenuList()
			}
			var self = this;
			setTimeout(function() {
				self.recommendTpl
				self.loading = false;
			}, 1000)
		},
		...mapActions({
			MenuList: 'Menu/SetLit',
			shudongAction: 'Menu/shudongAction',
			shudonglower: 'Menu/shudonglower',
			shudonRefresh: 'Menu/shudonRefresh',
		}),
		// 点击详情
		choose(item) {
			// item 返回选中 JSON 对象
			console.log(item.id)
		},
		async userDetails() {
			var res = await this.ajax.get(this.apiUrl.userDetails, {
				id: '5d9c9273865a9844a301a0a5'
			});
			console.log(res)
		},
	}
}
